// Core
import React, { Component } from 'react';
import {
    Transition,
    CSSTransition,
    TransitionGroup
} from 'react-transition-group';
import gsap from 'gsap';

// Instruments
import Styles from './styles.m.css';
import { GROUP_ID, api } from '../../REST';
import { socket } from '../../socket/init';

// Components
import {
    StatusBar,
    Composer,
    Post,
    Catcher,
    Counter,
    Spinner,
    Postman
} from '../../components';

export default class Feed extends Component {
    state = {
        posts:           [],
        isPostsFetching: false,
    };

    componentDidMount () {
        const { currentUserFirstName, currentUserLastName } = this.props;

        this._fetchPosts();
        socket.emit('join', GROUP_ID);

        socket.on('create', (postJSON) => {
            const { data: createdPost, meta } = JSON.parse(postJSON);

            if (
                `${currentUserFirstName} ${currentUserLastName}` !==
                `${meta.authorFirstName} ${meta.authorLastName}`
            ) {
                this.setState(({ posts }) => ({
                    posts: [createdPost, ...posts],
                }));
            }
        });

        socket.on('remove', (postJSON) => {
            const { data: removedPost, meta } = JSON.parse(postJSON);

            if (
                `${currentUserFirstName} ${currentUserLastName}` !==
                `${meta.authorFirstName} ${meta.authorLastName}`
            ) {
                this.setState(({ posts }) => ({
                    posts: posts.filter((post) => post.id !== removedPost.id),
                }));
            }
        });

        socket.on('like', (postJSON) => {
            const { data: likedPost, meta } = JSON.parse(postJSON);

            if (
                `${currentUserFirstName} ${currentUserLastName}` !==
                `${meta.authorFirstName} ${meta.authorLastName}`
            ) {
                this.setState(({ posts }) => ({
                    posts: posts.map(
                        (post) => post.id === likedPost.id ? likedPost : post,
                    ),
                }));
            }
        });
    }

    componentWillUnmount () {
        socket.removeListener('create');
        socket.removeListener('remove');
        socket.removeListener('like');
    }

    _setPostsFetchingState = (state) => {
        this.setState({
            isPostsFetching: state,
        });
    };

    _fetchPosts = async () => {
        try {
            this._setPostsFetchingState(true);

            const posts = await api.fetchPosts();

            this.setState(() => ({ posts, isPostsFetching: false }));
        } catch ({ message }) {
            console.log(message);
        } finally {
            this._setPostsFetchingState(false);
        }
    };

    _createPost = async (comment) => {
        try {
            this._setPostsFetchingState(true);

            const post = await api.createPost(comment);

            this.setState(({ posts }) => ({
                posts: [post, ...posts],
            }));
        } catch ({ message }) {
            console.log(message);
        } finally {
            this._setPostsFetchingState(false);
        }
    };

    _removePost = async (postId) => {
        try {
            this._setPostsFetchingState(true);

            await api.removePost(postId);

            this.setState(({ posts }) => ({
                posts: posts.filter((post) => post.id !== postId),
            }));
        } catch ({ message }) {
            console.log(message);
        } finally {
            this._setPostsFetchingState(false);
        }
    };

    _likePost = async (postId) => {
        try {
            this._setPostsFetchingState(true);

            const likedPost = await api.likePost(postId);

            this.setState(({ posts }) => ({
                posts: posts.map(
                    (post) => post.id === likedPost.id ? likedPost : post,
                ),
            }));
        } catch ({ message }) {
            console.log(message);
        } finally {
            this._setPostsFetchingState(false);
        }
    };

    _handleComposerAppear = (composer) => {
        gsap.fromTo(composer, 1, { opacity: 0 }, { opacity: 1 });
    };

    _handleCounterAppear = (counter) => {
        gsap.fromTo(counter, 1, { opacity: 0 }, { opacity: 1 });
    };

    _handlePostmanAppear = (postman) => {
        gsap.fromTo(postman, 1, { x: 400, opacity: 0 }, { x: 0, opacity: 1 });
    };

    _handlePostmanDisappear = (postman) => {
        gsap.fromTo(postman, 1, { x: 0, opacity: 1 }, { x: 400, opacity: 0 });
    };

    render () {
        const { posts, isPostsFetching } = this.state;

        const postsJSX = posts.map(
            ({ avatar, comment, created, firstName, id, lastName, likes }) => (
                <CSSTransition
                    classNames = { {
                        enter:       Styles.postInStart,
                        enterActive: Styles.postInEnd,
                        exit:        Styles.postOutStart,
                        exitActive:  Styles.postOutEnd,
                    } }
                    key = { id }
                    timeout = { { enter: 500, exit: 400 } }>
                    <Catcher>
                        <Post
                            _likePost = { this._likePost }
                            _removePost = { this._removePost }
                            avatar = { avatar }
                            comment = { comment }
                            created = { created }
                            firstName = { firstName }
                            id = { id }
                            lastName = { lastName }
                            likes = { likes }
                        />
                    </Catcher>
                </CSSTransition>
            ),
        );

        return (
            <section className = { Styles.feed }>
                <Spinner isSpinning = { isPostsFetching } />
                <StatusBar />
                <Transition
                    appear
                    in
                    timeout = { 1000 }
                    onEnter = { this._handleComposerAppear }>
                    <Composer _createPost = { this._createPost } />
                </Transition>
                <Transition
                    appear
                    in
                    timeout = { 1000 }
                    onEnter = { this._handleCounterAppear }>
                    <Counter count = { 474 } />
                </Transition>
                <TransitionGroup>{postsJSX}</TransitionGroup>
                <Transition
                    appear
                    in
                    timeout = { 3000 }
                    onEnter = { this._handlePostmanAppear }
                    onEntered = { this._handlePostmanDisappear }>
                    <Postman />
                </Transition>
            </section>
        );
    }
}
