// Core
import React, { Component } from 'react';
import { string, func, number, arrayOf, shape } from 'prop-types';
import moment from 'moment';

// Instruments
import Styles from './styles.m.css';

// Components
import Like from '../Like';
import { withProfile } from '../HOC/withProfile';

@withProfile
export default class Post extends Component {
    static propTypes = {
        _likePost:   func.isRequired,
        _removePost: func.isRequired,
        avatar:      string.isRequired,
        comment:     string.isRequired,
        created:     number.isRequired,
        firstName:   string.isRequired,
        id:          string.isRequired,
        lastName:    string.isRequired,
        likes:       arrayOf(
            shape({
                firstName: string.isRequired,
                lastName:  string.isRequired,
            }).isRequired,
        ).isRequired,
    };

    shouldComponentUpdate (nextProps) {
        return Object.entries(nextProps).some(
            ([key, value]) => this.props[key] !== value,
        );
    }

    _removePost = () => {
        const { _removePost, id } = this.props;

        _removePost(id);
    };

    _getCross = () => {
        const {
            firstName,
            lastName,
            currentUserFirstName,
            currentUserLastName,
        } = this.props;

        return `${firstName} ${lastName}` ===
            `${currentUserFirstName} ${currentUserLastName}` ? (
                <span className = { Styles.cross } onClick = { this._removePost } />
            ) : null;
    };

    render () {
        const {
            avatar,
            comment,
            created,
            firstName,
            id,
            lastName,
            likes,
            _likePost,
        } = this.props;

        const cross = this._getCross();

        return (
            <section className = { Styles.post }>
                {cross}
                <img src = { avatar } />
                <a>{`${firstName} ${lastName}`}</a>
                <time>{moment.unix(created).format('MMMM D h:mm:ss a')}</time>
                <p>{comment}</p>
                <Like _likePost = { _likePost } id = { id } likes = { likes } />
            </section>
        );
    }
}
